import * as React from 'react';
import { Inline, RenderNodeProps } from '@ali/4ever-cangjie';
import { zhiTagFactory as tagFactory } from '@ali/4ever-factory';
import GroupTag from './groupTag';

const createGroupPlugin = tagFactory(() => {
  return {
    tagType: 'group',
    configName: 'imTag',
    renderNode: (props: RenderNodeProps) => {
      const node = props.node as Inline;
      return (<GroupTag node={node} />);
    },
  };
});


export default createGroupPlugin;
